<template>
    <div>
        <div style="display: flex;align-items: center">
            <el-input
                class="addPosInput"
                style="margin-right: 10px"
                placeholder="..."
                icon="el-icon-edit"
                @keydown.enter.native="addPosition"
                v-model="pos.name">
            </el-input>
            <template>
                <el-button icon="el-icon-plus"  type="primary" @click="addPosition"></el-button>
            </template>
            <el-input
                class="addPosInput"
                style="margin-left: 10px;margin-right: 10px"
                placeholder="..."
                icon="el-icon-search"
                v-model="pos.searchname">
            </el-input>
            <el-button  icon="el-icon-search"  type="primary" @click="searchname"></el-button>
        </div>
        <div class="posManaMain">
            <el-table
                :data="positions"
                border
                v-loading="loading"
                element-loading-spinner="el-icon-loading"
                element-loading-text="..."
                element-loading-background="rgba(0, 0, 0, 0.8)"
                max-height="680px"
                @selection-change="handleSelectionChange"
                size="medium"
                stripe
                style="width: 100%">
                <el-table-column
                    type="selection"
                    align="center"
                    width="55">
                </el-table-column>
                <el-table-column
                    prop="id"
                    label="ID"
                    sortable
                    align="center"
                    width="70">
                </el-table-column>
                <el-table-column
                    prop="name"
                    align="center"
                    label=""
                    width="180">
                </el-table-column>
                <el-table-column
                    align="center"
                    prop="createdate"
                    width="200"
                    label="">
                </el-table-column>
                <el-table-column
                    align="center"
                    label=""
                    width="150">
                    <template slot-scope="scope">
                        <el-tag size="small" type="success" v-if="scope.row.enabled"></el-tag>
                        <el-tag size="small" type="danger" v-else></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="showEditView(scope.$index, scope.row)">
                        </el-button>
                        <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-button @click="deleteMany" type="danger" icon="el-icon-delete" style="margin-top: 15px" :disabled="multipleSelection.length==0"></el-button>
            <el-button @click="refershMany" type="success" icon="el-icon-refresh" style="margin-top: 8px"> </el-button>
        </div>
        <el-dialog
                title=""
                :visible.sync="dialogVisible"
                width="30%">
            <div>
                <el-form :rules="rules">
                    <el-form-item label="" prop="updatePos.name" label-width="100px">
                        <el-input type="text" class="updatePosInput" size="small" v-model="updatePos.name"/>
                    </el-form-item>
                    <el-form-item label="" prop="delivery" label-width="100px">
                        <el-switch v-model="updatePos.enabled" style="margin-left: 15px">
                        </el-switch>
                    </el-form-item>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
    <el-button size="small" @click="outUpdate"> </el-button>
    <el-button size="small" type="primary" @click="doUpdate"> </el-button>
  </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: "PosMana",
    data() {
        return {
            pos: {
                name: '',
                searchname: '',
            },
            dialogVisible: false,
            loading: false,
            updatePos: {
                name: '',
                enabled: false
            },
            multipleSelection: [],
            positions: [],
        }
    },
    mounted() {
        this.initPositions();
    },
    methods: {
        refershMany() {
            this.initPositions();
        },
        searchname() {
            if (this.pos.searchname) {
                this.$notify.success({
                    title: '',
                    message: '...',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
                this.pos.name = this.pos.searchname;
                this.postRequest("/system/basic/pos/serch/", this.pos).then(resp => {
                    if (resp) {
                        /*+*/
                        this.positions = resp;
                        this.pos.searchname = '';
                        this.pos.name = '';
                    }
                })
            } else {
                this.$notify({
                    title: '',
                    message: '        !',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass',
                    type: 'warning'
                });
            }
        },
        deleteMany() {
            this.$confirm('' + this.multipleSelection.length + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let ids = '?';
                this.multipleSelection.forEach(item => {
                    ids += 'ids=' + item.id + '&';
                });
                this.$notify.success({
                    title: '',
                    message: '...',
                    showClose: false,
                    offset: 100,
                    duration: 4000,
                    customClass: 'fontclass'
                });
                this.deleteRequest("/system/basic/pos/" + ids).then(resp => {
                    if (resp) {
                        this.initPositions();
                    }
                })
            }).catch(() => {
                this.$notify.info({
                    title: '',
                    message: '...',
                    showClose: false,
                    offset: 100,
                    duration: 4000,
                    customClass: 'fontclass'
                });
            });
        },
        /**/
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        /**/
        addPosition() {
            if (this.pos.name) {
                this.$notify.success({
                    title: '',
                    message: '...',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
                this.postRequest("/system/basic/pos/", this.pos).then(resp => {
                    if (resp) {
                        /*+*/
                        this.initPositions();
                        this.pos.name = '';
                    }
                })
            } else {
                this.$notify({
                    title: '',
                    message: '!',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass',
                    type: 'warning'
                });
            }
        },
        showEditView(index, data) {
            //
            /*sourcetarget*/
            Object.assign(this.updatePos, data);
            this.dialogVisible = true;
        },
        outUpdate() {
            this.$notify.info({
                title: '',
                message: '',
                showClose: false,
                offset: 100,
                duration: 2000,
                customClass: 'fontclass'
            });
            this.dialogVisible = false;
        },
        doUpdate() {
            this.$notify.success({
                title: '',
                message: '...',
                showClose: false,
                offset: 100,
                duration: 2000,
                customClass: 'fontclass'
            });
            this.putRequest("/system/basic/pos/", this.updatePos).then(resp => {
                if (resp) {
                    this.initPositions();
                    this.updatePos.name = '';
                    this.dialogVisible = false;
                }
            })
        },
        handleDelete(index, data) {
            this.$confirm('' + data.name + ', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                this.$notify.success({
                    title: '',
                    message: '...',
                    showClose: false,
                    offset: 100,
                    duration: 4000,
                    customClass: 'fontclass'
                });
                this.deleteRequest("/system/basic/pos/" + data.id).then(resp => {
                    if (resp) {
                        this.initPositions();
                    }
                })
            }).catch(() => {
                this.$notify.info({
                    title: '',
                    message: '',
                    showClose: false,
                    offset: 100,
                    duration: 2000,
                    customClass: 'fontclass'
                });
            });
        },
        /*AJAX*/
        initPositions() {
            setTimeout(() => {
            this.$notify.success({
                title: '',
                message: '...',
                showClose: false,
                offset: 100,
                duration: 2000,
                customClass: 'fontclass'
            });
            }, 900);
            this.loading = true;
            this.getRequest("/system/basic/pos/").then(resp => {
                // this.loading = false;
                if (resp) {
                    this.loading = false;
                    this.positions = resp;
                }
            })
        }
    }
}
</script>

<style>
.fontclass {
    font-size: 35px;
    font-family: ;
}
.addPosInput {
    width: 300px;
    /* margin: 0px 0px; */
}
.updatePosInput {
    width: 200px;
    margin-left: 8px;
}
.posManaMain {
    margin-top: 10px;
}
</style>